<template>
  <div class="bgs" v-bind:style="{ backgroundImage: 'url(bg/bglon2.webp),url(bg/bglon1.webp)' }">

    <b-row class="fh">
      <b-col cols="2">
        <LeftCom></LeftCom>
      </b-col>

      <b-col cols="8">

        <b-row class="bookview">
          <div class="book">
            <div id="pages" class="pages" v-if="kkk > 0">
              <div class="page" :class="getCl(0)" :style="getZindex(0)" @click="clicks(0)">
                <div class="book" v-bind:style="{ backgroundImage: 'url(bg/a.png), url(' + anh + ')' }"></div>
              </div>
              <div class="page2" :class="getCl(1)" :style="getZindex(1)">
                <Modau></Modau>
              </div>
              <!-- noi dung -->
              <div class="page scolls" :class="getCl(2)" :style="getZindex(2)" @click="clicks(2)">
                <div class="box-content">Khi phê phán Đuyrinh muốn đưa ra một học thuyết đạo đức vĩnh cửu, bất biến, đúng
                  với mọi thời đại lịch sử và mọi dân tộc, Ph.Ăngghen đã khẳng định: “Xét cho đến cùng, mọi học thuyết về
                  đạo đức đã có từ trước đến nay đều là sản phẩm của tình hình kinh tế cửa xã hội lúc bấy giờ”. Trong xã
                  hội có giai cấp, đạo đức mang tính giai cấp. Mỗi giai cấp đều có đạo đúc riêng, phản ánh những quan hệ
                  thực tiễn đang làm cơ sở cho vị trí giai cấp của mình, tức là những quan hệ kinh tế mà trong đó, người
                  ta tiến hành sản xuất và trao đổi. Và theo Ph.Ăngghen, cho đến nay “không có đạo đức nào là chân chính
                  cả, nếu nói theo ý nghĩa tuyệt đối cuối cùng” của nó. Tuy nhiên, Ph.Ăngghen cũng không hề phủ nhận giá
                  trị của các học thuyết đạo đức đối với sự phát triển xã hội. Ông cho rằng đối với đạo đức cũng như đối
                  với tất cả các ngành tri thức khác của nhân loại, nói chung người ta thấy có một sự tiến bộ đó là điều
                  không còn nghi ngờ gì nữa. Và “thứ đạo đức hiện nay đang tiêu biểu cho sự lật đổ hiện tại, biểu hiện cho
                  lợi ích của tương lai, tức là đạo đức vô sản, là thứ đạo đức có một số lượng nhiều nhất nhưng nhân tố
                  hứa hẹn một sự tồn tại lâu dài”. <br>
                  <br>
                  Như vậy, sự tiến bộ của đạo đức là do những giá trị đạo đức trong lịch sứ phát triển của nó tạo thành.
                  Trong các học thuyết về đạo đức, có học thuyết chỉ có giá trị thúc đẩy tiến bộ xã hội ở một thời điểm
                  nhất định nào đó , nhưng cũng có những học thuyết đạo đức có thể có giá trị lâu dài đối với sự phát
                  triển xã hội. <br>

                </div>
              </div>
              <div class="page scolls" :class="getCl(3)" :style="getZindex(3)" @click="clicks(3)">
                <div class="box-content">Vậy giá trị đạo đức là gì? Dựa vào tiêu chuẩn nào để đánh giá giá trị đạo đức?
                  Như chúng ta đã biết, đạo đức với tính cách là một hình thái ý thức xã hội có thể được xem là đoàn bộ
                  những quan niệm về thiện, ác, tất, xấu, lương tâm, trách nhiệm, hạnh phúc, công bằng… và về những quy
                  tắc đánh giá, điều chỉnh hành vi ứng xử giữa cá nhân với xã hội, giữa cá nhân với cá nhân trong xã hội.
                  <br>
                  <br>
                  Trong xã hội có giai cấp, tư tưởng đạo đức thống trị trong xã hội là tư tưởng đạo đức của giai cấp giữ
                  địa vị thống trị về kinh tế. Giai cấp giữ địa vị thống trị về kinh tế dựa vào bộ máy nhà nước để tuyên
                  truyền, giáo dục và thể chế hoá tư tưởng đạo đức của mình thành những nguyên tắc chuẩn mực đạo đức, biến
                  nó trở thành thước đo đánh giá, điều chỉnh hành vi của các cá nhân trong xã hội, phù hợp với lợi ích của
                  nó. <br>

                </div>
              </div>
              <div class="page scolls" :class="getCl(4)" :style="getZindex(4)" @click="clicks(4)">
                <div class="box-content">Giá trị, theo nghĩa chung nhất, chúng ta có thể hiểu nó là cái mà do nó đã làm
                  cho một khách thể nào đó có ích, có nghĩa, đáng quý đối với chủ thể, được mọi người thừa nhận. Người ta
                  có thể phân chia giá trị thành: giá trị vật chất, giá trị tinh thần, giá trị riêng, giá trị chung, giá
                  trị xã hội. Có quan điểm cho rằng giá trị là cái vốn có của khách thể, nhưng mỗi chủ thể lại có thể đánh
                  giá, xem xét nó trên những góc độ khác nhau, đưa đến những quan niệm khác nhau về giá trị. Theo chúng
                  tôi, quan điểm đó không đúng, bởi khi nói đến giá trị là nói đến cái được mọi người thừa nhận, phù hợp
                  với lợi ích của xã hội. Tuy nhiên, giá trị cũng là phạm trù có tính lịch sử. <br>
                  <br>
                  Xuất phát từ những quan niệm trên, chúng ta có thể hiểu giá trị đạo đức là cái được con người lựa chọn
                  và đánh giá, xem nó như là việc làm có ý nghĩa tích cực đối với đời sống xã hội, được lương tâm đồng
                  tình và dư luận biểu dương. <br>

                </div>
              </div>
              <div class="page scolls" :class="getCl(5)" :style="getZindex(5)" @click="clicks(5)">
                <div class="box-content">Lợi ích xã hội là tiêu chuẩn khách quan của các giá trị đạo đức, do đó chỉ khi
                  nào những nguyên tắc, chuẩn mực đạo đức điều chỉnh hành vi phù hợp với lợi ích xã hội, được dư luận đồng
                  tình ủng hộ thì mới có giá trị. Như vậy, giá trị đạo đức được xác định bởi mức độ phù hợp của chúng đối
                  với yêu cầu của tiến bộ xã hội. Song lợi ích xã hội và yêu cầu của tiến bộ xã hội lại có tính lịch sử,
                  nghĩa là mỗi giai đoạn phát triển xã hội lại có những yêu cầu về lợi ích và sự tiến bộ của nó, chính vì
                  vậy mà giá trị đạo đức cũng có tính lịch sử. Ph.Ăngghen cho rằng lời răn đạo đức “không được trộm cắp”
                  chỉ có giá trị ở các xã hội có chế độ sở hữu tư nhân về động sản, còn “trong một xã hội mà mọi động cơ
                  đẩy tới trộm cắp đều bị loại trừ, do đó đần dần hầu như chỉ cô những người mắc bệnh tinh thần mới phạm
                  tội trộm cắp, thì một nhà truyền bá đạo đức nào muốn trịnh trọng tuyên bố cái chân lý vĩnh cửu: không
                  được trộm cắp, sẽ bị người ta chê cười”. <br>
                  <br>
                  Giá trị đạo đức được thể hiện thông qua việc thực hiện các chức năng của đạo đức. Song, ở giai đoạn nào
                  của sự phát triển xã hội thì đạo đức, theo chúng tôi, cũng có những chức năng cơ bản sau: <br>

                </div>
              </div>
              <div class="page scolls" :class="getCl(6)" :style="getZindex(6)" @click="clicks(6)">
                <div class="box-content">Thứ nhất, chức năng điều chỉnh hành vi. Sự điều chỉnh hành vi được thực hiện bằng
                  hai hình thức chủ yếu. Một là, thông qua dư luận xã hội, ca ngợi, khuyến khích cái thiện, cái tốt, lên
                  án, phê phán cái ác, cái xấu. Trong trường hợp này, giá trị đạo đức phụ thuộc vào sức mạnh và tính đúng
                  đắn của dư luận. Mỗi khi dư luận xã hội được củng cố và phát triển, được mọi người đồng tình ủng hộ, nó
                  sẽ trở thành sức mạnh to lớn trong việc điều chỉnh đạo đức. Dân tộc ta có câu ca: “Trăm năm bia đá thì
                  mòn, ngàn năm bia miệng vẫn còn trơ trơ”. Quả thật, cái “bia miệng” (dư luận xã hội) hàng ngàn năm vẫn
                  lưu truyền, nó lâu bền hơn cả sắt đá nó trở thành công cụ lợi hại trong việc điều chỉnh hành vi đạo đức.
                  Do đó, nếu dư luận xã hội mỗi khi được hướng dẫn bởi những học thuyết đạo đức tiên tiến, sẽ góp phần
                  điều chỉnh hành vi đạo đức, làm cho nó phù hợp với sự tiến bộ xã hội, tạo nên nhưng giá trị đạo đức đích
                  thực. Hai là, bản thân chủ thể đạo đức tự giác điều chỉnh hành vi của mình theo những chuẩn mực đạo đức
                  xã hội. Cách thức điều chỉnh này phụ thuộc vào việc giáo dục, giác ngộ của chủ thể đạo đức. Bởi lẽ,
                  trong quan hệ đạo đức chủ thể đạo đức vừa tham gia vào hành vi ứng xử, vừa là người phán xét hành vi ứng
                  xử của chính mình. Khi thực hiện hành vi ứng xử, chủ thể đạo đức dựa vào các chuẩn mực được hình thành
                  trong bản thân họ (chuẩn mực này đã tiếp thu chuẩn mực đạo đức xã hội và do điều kiện sinh sống và các
                  quan hệ xã hội cụ thể của cá nhân chủ thể tạo thành). Nếu các chuẩn mực đó phù hợp với chuẩn mực đạo đức
                  xã hội, nó sẽ trở thành cơ sở khách quan làm cho hành vi đạo đức của cá nhân phù hợp với lợi ích xã hội,
                  được dư luận đồng tình ủng hộ. Ngược lại, mỗi khi chuẩn mực cá nhân sai lệch chuẩn mực xã hội se dẫn đến
                  những hành vi đạo đức cá nhân không phù hợp với lợi ích xã hội. Trong trường hợp này, dư luận xã hội sẽ
                  lên án, phê phán. Khi phán xét ‘hành vi ứng xử của mình, chủ thể đạo đức căn cứ vào chuẩn mực đạo đức
                  của cá nhân họ. Nếu như vì thiếu nhiệt tình, chưa cố gắng hết sức mình, nên không thực hiện được một
                  quan hệ đạo đức nào đó đúng như chuẩn mực của bản thân, thì lương tâm họ bị cắn rứt, họ cảm thấy ân hận,
                  xấu hổ về hành vi của mình. Ngược lại mỗi khi họ thực hiện được những quan hệ đạo đức đúng như chuẩn mực
                  đã hình thành ở họ, thì lương tâm họ cảm thấy thanh thản, họ tự hào về hành vi của mình. Như vậy, với
                  cách thức điều chỉnh này, giá trị đạo đức phụ thuộc vào các chuẩn mực đạo đức được hình thành trong mỗi
                  chủ thể đạo đức đó là cơ sở cho hành vi đạo đức của mỗi cá nhân phù hợp với lợi ích và sự tiến bộ xã
                  hội. <br>
                  <br>
                  Thứ hai, chức năng giáo dục. Chức năng giáo dục được thực hiện thông qua sự giáo dục của xã hội và sự tự
                  giáo dục của mỗi cá nhân. Giáo dục đạo đức là quá trình tuyên truyền những tư tương, nhưng chuẩn mực đạo
                  đức xã hội, biến nó thành thước đo đánh giá, điều chình hành vi của mỗi cá nhân nhằm đạt tới một sự phù
                  hợp giữa hành vi cá nhân với lợi ích xã hội. <br>

                </div>
              </div>
              <div class="page scolls" :class="getCl(7)" :style="getZindex(7)" @click="clicks(7)">
                <div class="box-content"> Trong quá trình hoạt động sinh sống, mỗi cá nhân không chỉ mưu cầu lợi ích vật
                  chất, lợi ích tinh thần, mà còn mưu cầu sự tiến bộ của bản thân và đều muốn được dư luận xã hội ca ngợi,
                  biểu đương. Do đó, những tư tưởng và những chuẩn mực đạo đức xã hội trở thành mục tiêu, thành những định
                  hướng cho hoạt động cá nhân của nó. Những hành vi đạo đức có tính chất lặp đi lặp lại, tạo nên các quan
                  hệ đạo đức tương đối ổn định và nó thường xuyên được duy trì, củng cố bằng dư luận xã hội sẽ trở thành
                  thói quen, tập quán, truyền thống đạo đức trở thành nhân tố trực tiếp tác động đến sự hình thành đạo đức
                  của thế hệ người tiếp theo. Như vậy, giá trị đạo đức trong trường hợp này được hình thành, phát triển ra
                  sao luôn phụ thuộc vào tính đúng đắn, khoa học của những tư tưởng và những chuẩn mực đạo đức được tuyên
                  truyền, giáo đục trong xã hội. <br>
                  <br>
                  Tự giáo dục đạo đức của cá nhân, trước hết thế hiện ở chỗ, môi cá nhân thông qua sự tự phán xét của
                  lương tâm về hành vi của mình để củng cố các chuẩn mực đạo đức cá nhân, để đạt tới hành vi ứng xử phù
                  hợp với các chuẩn mực đạo đức đó. Mặt khác, dựa vào dư luận xã hội, họ tự điều chỉnh hành vi và điều
                  chỉnh ngay cả những chuẩn mực đạo đức cá nhân khi nhận thấy nó sai lệch với chuẩn mực xã hội. Giá trị
                  đạo đức trong trường hợp này được xác định phụ thuộc vào sự nhạy cảm, sự cầu thị của chủ thể trong việc
                  tu dưỡng, rèn luyện đạo đức cho phù hợp với tư tưởng, chuẩn mực đạo đức của xã hội. <br>

                </div>
              </div>
              <div class="page scolls" :class="getCl(8)" :style="getZindex(8)" @click="clicks(8)">
                <div class="box-content"> Thứ ba, chức năng nhận thức. Những tư tưởng đạo đức và chuẩn mực đạo đức xã hội
                  có trở thành các quan hệ đạo đức trong đời sống xã hội hay không, điều đó không chỉ phụ thuộc vào tính
                  đúng đắn, tư tưởng đạo đức, của các chuẩn đạo đức, vào việc tuyên truyền, giáo dục trong xã hội, mà còn
                  phụ thuộc rất lớn vào khả năng tiếp nhận và chuyển hoá nó trong hoạt động nhận thức và trong hành vi của
                  mỗi chủ thể đạo đức. Thông qua sự lựa chọn, đánh giá của các chủ thể đạo đức về những tư tưởng, chuẩn
                  mực đạo đức, trong bản thân họ hình thành niềm tin, lý tương đạo đức và các nguyên tắc, chuẩn mực đạo
                  đức trong quan hệ ứng xử của chính họ. <br>
                  <br>
                  Hoạt động nhận thức đạo đức cũng bao gồm hai trình độ: tình cảm và tư tưởng đạo đức, kinh nghiệm và lý
                  luận đạo đức. <br>

                </div>
              </div>
              <div class="page scolls" :class="getCl(9)" :style="getZindex(9)" @click="clicks(9)">
                <div class="box-content"> Nhận thức đạo đức ớ trình độ kinh nghiệm hoặc tình cảm đạo đức gắn liền với hoạt
                  động thực tiễn, với các quan hệ đạo đức của xã hội và luôn chịu ảnh hưởng của dư luận, tập quán, truyền
                  thống đạo đức. Quá trình này hình thành nên ớ chủ thể đạo đức nhưng hình ảnh, những “thần tượng” được
                  coi là chuẩn mực trực tiếp cho việc đánh giá, điều chỉnh hành vi đạo đức của họ. Chẳng hạn, một đứa trẻ
                  thường thông qua việc quan sát những hành vi ứng xử cửa bố mẹ nó vời bạn bè, với nhưng người thân trong
                  gia đình, với công việc… nó cũng sẽ làm như bố mẹ đã làm trước đây. <br>
                  <br>
                  Nhận thức đạo đức ở trình độ tư tưởng hoặc lý luận là nhận thức có tính nguyên tắc được chỉ đạo bới
                  những lợi ích căn bản của giai cấp, của xã hội. <br>

                </div>
              </div>
